.item {
  width: 350px;
  gap: 30;
}

.item p {
  margin: 6px 0px;
}
.items-prices {
  display: flex;
  gap: 20px;
}
.items-price-new {
  color: #374151;
  font-size: 18px;
  font-weight: 600;
}

.items-price-old {
  color: #8c8c8c;
  font-size: 18px;
  font-weight: 500;
  text-decoration: line-through;
}
.item:hover {
  transform: scale(1.05);
  transition: 0.6;
}
